body{
    margin: 0;
    padding: 0;
}

hr {
    border-top: 2px solid rgb(255, 255, 255);
}

.navbar{
    /***** stay on top *****/
    position: -webkit-sticky;
    position: fixed;
    top: 0;

    width: 100%;
    display: flex;
    /*padding: 5px 5px;*/
    /*margin: auto;*/
    /*background: rgb(118, 112, 206);*/
    background-image:  linear-gradient(to right, rgba(0, 0, 255, 1), rgba(0, 0, 255, 0.5));

}

.navitem{
    margin: auto;
    padding: 0;
    color: rgb(203, 196, 226);
    font-size: 20px;
    text-shadow: 1px -1px rgb(226, 214, 214);
}

.navitem p::after{
    content: "";
    display: block;
    height: 2px;
    width: 0px;
    background: chartreuse;
    transition: width ease-out 300ms;
}

.navitem p:hover::after{
    width: 100%;
}

.navitem p:hover{
    transform: scale(1.1);
    /*color: rgb(103, 105, 182);*/
    /*text-decoration-line: underline;*/
    cursor: pointer;
}

.logoicon{
    height: 40px;
    width: 40px;
    padding: 5px 12px;
}

.logolink{
    height: 40px;
    width: 40px;
    padding: 5px 12px;
    color: rgb(0, 0, 200);
    filter: drop-shadow(0px 1px grey);
}

/********** Add tooltip ***********/
[tooltip]{
    position: relative;
}

[tooltip]::after{
    position: absolute;
    margin: 0 auto;
    display: block;
    content: attr(tooltip);
    border-radius: 5px;
    padding: 5px;
    background: grey;
    width: fit-content;
    color: honeydew;
    
    bottom: -30px;
    left: -50%;

    white-space: nowrap; /*ser till att allt kommer p√• en rad*/
    /*animation*/
    transform: scale(0);
    transition: transform ease-out 400ms;
}
[tooltip]:hover::after{
    transform: scale(1);
}

.logolink:hover{
    transform: scale(1.3);
    /*color: rgb(0, 0, 200, 0.5);*/
    filter: drop-shadow(0px 3px grey);
}


